<template>
	<view class="content">
		<!--bill-title  -->
		<view class="bill">
			<view class="bill-num">订单编号 123456789644236</view>
			<view class="bill-price">开票金额   ￥160</view>
		</view>
		<u-form :model="form" ref="uForm">
			<u-form-item label="抬头类型">
				<u-radio-group v-model="radio">
					<u-radio active-color="#F86032" v-for="(item, index) in radioList" :key="index" :name="item.name" :disabled="item.disabled" @change="radioChange" >
						{{ item.name }}{{item.checked}}
					</u-radio>
				</u-radio-group>
			</u-form-item>
			<u-form-item label="发票抬头" prop="name">
				<u-input v-model="form.name" placeholder="填写需要开票的个人姓名" />
			</u-form-item>
			<u-form-item label="个人地址" prop="address">
				<u-input v-model="form.address" placeholder="填写需要开票的个人地址" />
			</u-form-item>
			<u-form-item label="个人地址" prop="tel">
				<u-input v-model="form.tel" placeholder="填写需要开票的个人电话" />
			</u-form-item>
		</u-form>
		<u-button @click="submit">提交申请</u-button>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				form: {
					name: '',
					intro: '',
				},
				radio: '个人',//默认选中
				radioList: [
					{
						name: '个人',
						disabled: false,
					},
					{
						name: '企业',
						disabled: false,
	
					},
				],
				rules: {
					name: [{
						required: true,
						message: '请填写需要开票的个人姓名',
						// 可以单个或者同时写两个触发验证方式 
						trigger: ['change', 'blur'],
					}],
					address: [{
						min: 5,
						message: '请填写需要开票的个人地址',
						trigger: 'change'
					}],
					tel: [{
						min: 11,
						max: 11,
						message: '请填写需要开票的11位手机号码',
						trigger: 'change'
					}],
					
				}
			};
		},
		methods: {
			submit() {
				this.$refs.uForm.validate(valid => {
					if (valid) {
						console.log('验证通过');
					} else {
						console.log('验证失败');
					}
				});
			},
			// 选中某个单选框时，由radio时触发
					radioChange(e) {
						console.log(e);
						if(e == "企业"){
							//弹出遮罩
						}
					},
		},
		// 必须要在onReady生命周期，因为onLoad生命周期组件可能尚未创建完毕
		onReady() {
			this.$refs.uForm.setRules(this.rules);
		}
	};
</script>
<style scoped>
	uni-page {
	    background: #F2F2F2;
	}
	.content{
		background-color: #F2F2F2;
		box-sizing: border-box;
	}
	/* bill-title */
	.bill{
		margin-left: 25upx;
		margin-right: 25upx;
		border-radius: 8upx;
		background-color: #FFFFFF;
		padding: 25upx;
		margin-top: 25upx;
		font-size:30upx ;
		
	}
	.bill-num{
		line-height: 1.8;
		color: #4D4D4D;
	}
	.bill-price{
		line-height: 1.8;
		color: #4D4D4D;
	}
</style>
